.card{
    max-width: 300px;
    min-width: 250px;
    height: 500px;
    border: 1px solid #645e9d;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
  }
  .card-title{
      font-size: 1.3em;
  }
  .card-img{
    width: 99%;
  }
  .card-img img{
    width: 100%;
    max-height: 300px;
  }
  .btn{
    background-color: #392b58;
    color: white;
    border-radius: 20px;
    padding: 20px;
    text-decoration: none;
    font-weight: bold;
  }