.container{
    margin-top: 60px;
}
.detalles-img{
    width: 100%;
    max-height: 400px;
}
.detalles{
    border-bottom: 2px solid #99d5c9;
    color: #392b58;
    margin:0;
    height: 90px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.detalles span{
    padding: 10px;
}
.loading{
    position: fixed;
    top: 50%;
}
.texto{
    padding: 20px;
}
@media screen and (min-width: 700px) {
    .container{
        display: flex;
        margin: 90px;
        justify-content: space-around;
    }
    .detalles-img{
        max-width: 60%;
    }
    .detalles-text{
        width: 30%;
    }
}